.modal-block {
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: calc(100% - 80px);
  box-sizing: border-box;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  -webkit-overflow-scrolling: touch;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal-block__container {
  width: 100%;
  box-sizing: border-box;
  padding-left: 32px;
  padding-right: 32px;
}
.modal-block__content {
  position: relative;
  background-color: #ffffff;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  height: 100%;
}
.modal-block__content-wrapper {
  overflow-y: auto;
  height: 100%;
}
.modal-block__content-inner {
  padding-top: 64px;
  padding-bottom: 90px;
  max-width: 460px;
  margin-left: auto;
  margin-right: auto;
}
@media (max-width: 767px) {
  .modal-block__content-inner {
    padding-top: 40px;
  }
}
.modal-block__image {
  margin-bottom: 40px;
}
.modal-block__image img {
  display: block;
  width: 100%;
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
}
.modal-block__title {
  font-size: 25px;
  text-align: center;
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 25px;
  color: #333333;
}
.modal-block__text p {
  font-size: 18px;
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
  color: #333333;
}
.modal-block__text p + p {
  margin-top: 16px;
}
.modal-block__button {
  margin-top: 64px;
}
.modal-block__close {
  position: absolute;
  content: "";
  bottom: calc(100% + 18px);
  right: 24px;
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
