.checkbox {
  display: flex;
  align-items: top;
  cursor: pointer;
}
.checkbox__input-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
}
.checkbox__input-hidden:checked + .checkbox__input {
  position: relative;
  background-color: #333333;
}
.checkbox__input-hidden:checked + .checkbox__input::before {
  transform: scale(1);
}
.checkbox__input {
  width: 16px;
  height: 16px;
  border-radius: 3px;
  border: 1px solid #333333;
  box-sizing: border-box;
  display: flex;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
  margin-top: 2px;
}
.checkbox__input::before {
  position: absolute;
  content: "";
  width: 14px;
  height: 14px;
  display: block;
  left: 0;
  top: 0;
  border-radius: 3px;
  background-image: url("../img/svg/checkbox-checked.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #ffffff;
  transition: transform 0.1s;
  transform-origin: center;
  transform: scale(0);
}
.checkbox__content {
  margin-left: 9px;
  font-size: 11px;
  line-height: normal;
  display: flex;
  align-items: center;
  color: #343a40;
  font-weight: 500;
}

.field-promo {
  width: 100%;
